import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Box,
  Container,
  Typography,
} from '@material-ui/core';
import React from 'react';
import { useQuery } from 'react-query';
import faq from '../services/kbs/faq';
import Loading from '../components/loading';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const Faq = () => {
  const { isLoading, data } = useQuery('faq', faq);

  if (isLoading) {
    return <Loading />;
  }

  console.log(data);

  return (
    <div style={{ minHeight: '70vh' }}>
      <Container>
        <Typography
          variant='h5'
          align='center'
          style={{ margin: '2rem 0', color: 'grey' }}
        >
          Frequenlty Asked Questions
        </Typography>
        <Box>
          {data.map((item, i) => (
            <Accordion key={i} style={{ marginBottom: '1rem' }}>
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls='panel1a-content'
                id='panel1a-header'
              >
                <Typography variant='h6'>{item.question}</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography>{item.answer}</Typography>
              </AccordionDetails>
            </Accordion>
          ))}
        </Box>
      </Container>
    </div>
  );
};

export default Faq;
