import React from 'react';
import { makeStyles, Typography } from '@material-ui/core';
import TestimonialCard from './testimonialCard';
import Slider from 'react-slick';
import { useQuery } from 'react-query';
import getTestimonials from '../services/user/getTestimonials';
import Loading from '../components/loading';
import { ArrowBack, ArrowForward } from '@material-ui/icons';

const useStyles = makeStyles((theme) => ({
  sliderContainer: {
    maxWidth: '90%',
    marginLeft: '4rem',
    [theme.breakpoints.down('sm')]: {
      marginLeft: '1rem',
    },
  },
}));

const Testimonials = () => {
  const classes = useStyles();

  const { isLoading, data } = useQuery('testimonials', getTestimonials);

  const SampleNextArrow = (props) => {
    const { className, onClick } = props;
    return (
      <div className={className} onClick={onClick}>
        <ArrowForward
          size={30}
          style={{
            color: "#000",
            background: "#FFFFFF",
            border: "1px solid #000",
            borderRadius: "50%",
            padding: "4px",
          }}
        />
      </div>
    );
  }

  const SamplePrevArrow = (props) => {
    const { className, onClick } = props;
    return (
      <div className={className} onClick={onClick}>
        <ArrowBack
          size={30}
          style={{
            color: "#000",
            background: "#FFFFFF",
            border: "1px solid #000",
            borderRadius: "50%",
            padding: "4px",
          }}
        />
      </div>
    );
  }

  var settings = {
    dots: false,
    infinite: true,
    autoplay: true,
    centerMode: false,
    speed: 1000,
    autoplaySpeed: 3000,
    slidesToShow: 3,
    slidesToScroll: 2,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
        },
      },
      {
        breakpoint: 900,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  if (isLoading) {
    return <Loading />;
  }

  return (
    <>
      <Typography
        color='textSecondary'
        variant='h5'
        gutterBottom
        align='center'
        style={{ marginTop: '2rem', fontWeight: 'bold' }}
      >
        CUSTOMER TESTIMONIALS
      </Typography>
      <div className={classes.sliderContainer} align="middle">
        <Slider {...settings}>
          {data.map((person) => {
            return (
              <TestimonialCard
                key={person.id}
                name={person.name}
                image={`${process.env.REACT_APP_UPLOAD_URL}${person.image}`}
                review={person.testimonial}
                title={person.title}
              />
            );
          })}
        </Slider>
      </div>
    </>
  );
};

export default Testimonials;
