import {
  Button,
  FilledInput,
  makeStyles,
  MenuItem,
  Modal,
  Select,
  Typography,
} from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useMutation, useQueryClient, useQuery } from 'react-query';
import validator from 'validator';
import updateUserAddress from '../services/user/updateUserAddress';
import getUserAddressById from '../services/user/getUserAddressById';

const useStyles = makeStyles((theme) => ({
  container: {
    width: '40%',
    margin: 'auto',
    marginTop: '20vh',
    textAlign: 'center',
    [theme.breakpoints.down('xs')]: {
      width: '95%',
    },
  },
  fields: {
    padding: '10px',
    backgroundColor: 'white',
  },
}));

const UpdateAddressInfo = ({ open, handleClose, id }) => {
  const classes = useStyles();
  const [addressInfo, setAddressInfo] = useState({
    fullname: '',
    address: '',
    nearest_landmark: '',
    country: 'Nepal',
    phone: '',
  });

  const { refetch } = useQuery(['address', id], () => getUserAddressById(id), {
    enabled: false,
    onSuccess: (data) => {
      setAddressInfo({
        fullname: data.fullname,
        address: data.address,
        nearest_landmark: data.nearest_landmark,
        country: data.country,
        phone: data.phone,
      });
    },
  });

  useEffect(() => {
    refetch();
  }, []);

  const [error, setError] = useState(null);
  const mutation = useMutation((newAddress) =>
    updateUserAddress(newAddress, id)
  );
  const queryClient = useQueryClient();

  const handleAddressUpdateSubmit = (e) => {
    e.preventDefault();
    if (validator.isEmpty(addressInfo.fullname, { ignore_whitespace: true })) {
      setError('please enter valid full name');
      return;
    } else if (!validator.isMobilePhone(addressInfo.phone, ['ne-NP'])) {
      setError('please enter valid mobile number');
      return;
    } else if (
      validator.isEmpty(addressInfo.address, { ignore_whitespace: true })
    ) {
      setError('please enter valid address');
      return;
    }
    mutation.mutate(addressInfo, {
      onSuccess: () => {
        queryClient.invalidateQueries('userAddresses');
      },
    });
  };

  if (mutation.isSuccess) {
    handleClose();
  }

  if (mutation.isError) {
    setError(mutation.error.message);
  }

  return (
    <Modal open={open} onClose={handleClose} className={classes.container}>
      <form className={classes.fields} onChange={() => setError('')}>
        <Typography variant='h5' gutterBottom>
          Update Address Information
        </Typography>
        {error && (
          <Typography variant='subtitle1' color='error' gutterBottom>
            {error}
          </Typography>
        )}
        <FilledInput
          type='text'
          margin='dense'
          name='fullname'
          fullWidth
          value={addressInfo.fullname}
          onChange={(e) =>
            setAddressInfo({ ...addressInfo, fullname: e.target.value })
          }
          placeholder='Enter your full name'
          style={{ marginBottom: '10px' }}
        />
        <FilledInput
          type='text'
          margin='dense'
          name='address'
          value={addressInfo.address}
          placeholder='Your address eg. New Road, Mahadevsthan'
          onChange={(e) =>
            setAddressInfo({ ...addressInfo, address: e.target.value })
          }
          fullWidth
          style={{ marginBottom: '10px' }}
        />
        <FilledInput
          type='text'
          margin='dense'
          name='nearest_landmark'
          fullWidth
          placeholder='Nearest Landmark(optional) Near NMB Bank'
          value={addressInfo.nearest_landmark}
          onChange={(e) =>
            setAddressInfo({ ...addressInfo, nearest_landmark: e.target.value })
          }
          style={{ marginBottom: '10px' }}
        />

        <FilledInput
          type='text'
          margin='dense'
          name='country'
          fullWidth
          placeholder='Country'
          value={addressInfo.country}
          onChange={(e) =>
            setAddressInfo({ ...addressInfo, country: e.target.value })
          }
          style={{ marginBottom: '10px' }}
        />

        <FilledInput
          type='text'
          margin='dense'
          name='phone'
          fullWidth
          value={addressInfo.phone}
          onChange={(e) =>
            setAddressInfo({ ...addressInfo, phone: e.target.value })
          }
          placeholder='Phone'
          style={{ marginBottom: '10px' }}
        />
        <Button
          variant='outlined'
          color='primary'
          type='submit'
          onClick={handleAddressUpdateSubmit}
        >
          Update
        </Button>
      </form>
    </Modal>
  );
};

export default UpdateAddressInfo;
