import { Box, Container, Divider, Grid, Typography } from '@material-ui/core';
import React from 'react';
import { useQuery } from 'react-query';
import { useParams } from 'react-router';
import Loading from '../components/loading';
import OrderProductCard from '../components/orderProductCard';
import getOrderDetail from '../services/user/getOrderDetail';

const OrderDetails = () => {
  const { id } = useParams();

  const { data, isLoading } = useQuery(['order-details', id], () =>
    getOrderDetail(id)
  );

  console.log('orders', data);

  const showOrderItems = () => {
    return data[0].products.map((product) => {
      return (
        <OrderProductCard
          name={product.product.product_name}
          subtitle={product.product.brand && product.product.brand.brand_name}
          price={product.product.price}
          image={`${process.env.REACT_APP_UPLOAD_URL}${product.product.images[0].image}`}
          brandName={product.product.brand && product.product.brand.brand_name}
          bestSeller={product.product.best_seller}
          id={product.product.id}
          key={product.product.id}
          discount={product.product.discount_amount}
          quantity={product.quantity}
        />
      );
    });
  };

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div style={{ minHeight: '65vh' }}>
      <Container>
        <Typography
          style={{ margin: '2rem 0', color: 'grey' }}
          align='center'
          variant='h4'
        >
          Order Details
        </Typography>
        <Typography style={{ color: 'grey' }} variant='h5' gutterBottom>
          Ordered Products
        </Typography>
        <Box display='flex' justifyContent='space-between' flexWrap='wrap'>
          {showOrderItems()}
        </Box>

        <Typography
          style={{ color: 'grey', marginTop: '3rem' }}
          variant='h5'
          gutterBottom
        >
          Details
        </Typography>
        <Grid container style={{ margin: '2rem 0' }}>
          <Grid xs={12} md={6} item>
            <Typography gutterBottom variant='h6'>
              Order Id: {data[0].order_number}
            </Typography>
            <Typography gutterBottom variant='h6'>
              Delivery Cost: NPR. {data[0].cost[0].delivery_cost}
            </Typography>
            <Typography gutterBottom variant='h6'>
              Order Subtotal: NPR. {data[0].cost[0].order_subtotal}
            </Typography>
            <Typography gutterBottom variant='h6'>
              Total cost: NPR. {data[0].cost[0].total}
            </Typography>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography gutterBottom variant='h6'>
              Customer Name:{' '}
              {data[0].customer.first_name + ' ' + data[0].customer.last_name}
            </Typography>
            <Typography gutterBottom variant='h6'>
              Delivery Method: {data[0].delivery_method}
            </Typography>

            <Typography gutterBottom variant='h6'>
              Location: {data[0].products[0].order.shipping_address.address}
            </Typography>
            <Typography gutterBottom variant='h6'>
              Phone: {data[0].products[0].order.shipping_address.phone}
            </Typography>
            <Typography gutterBottom variant='h6'>
              Payment Method: {data[0].payment_method}
            </Typography>
            <Typography gutterBottom variant='h6'>
              Payment Status: {data[0].payment_status}
            </Typography>
          </Grid>
        </Grid>
      </Container>
    </div>
  );
};

export default OrderDetails;
