import {
  Box,
  Button,
  ButtonGroup,
  Chip,
  Divider,
  Grid,
  IconButton,
  makeStyles,
  Typography,
} from '@material-ui/core';
import { Rating } from '@material-ui/lab';
import React, { useState, useEffect, useContext } from 'react';
import { useHistory } from 'react-router';
import { toast } from 'react-toastify';
import { CartContext } from '../contexts/CartContext';
// import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { useMutation } from 'react-query';
import notifyStock from '../services/products/notifyStock';
import ReactImageMagnify from 'react-image-magnify';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';

const useStyles = makeStyles((theme) => ({
  previewImages: {
    marginTop: '1rem',
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
  },
  previewImages_item: {
    width: '70px',
    margin: '0 10px',
    borderRadius: '50%',
    objectFit: 'contain',
    cursor: 'pointer',
    border: `1px solid ${theme.palette.primary.main}`,
    marginTop: '10px',
  },
  image: {
    width: '400px',
    [theme.breakpoints.down('xs')]: {
      width: '300px',
    },
  },
  productDesc: {
    fontSize: '0.9rem',
    marginTop: '3rem',
    padding: '0 7rem',
    [theme.breakpoints.down('xs')]: {
      padding: '0 1rem',
    },
  },
  gap: {
    [theme.breakpoints.up('md')]: {
      marginRight: '3rem',
    },
  },
}));

const ProductInfo = ({ images, details, othersizes }) => {
  const [imageNumber, setImageNumber] = useState(0);
  const [imageSrc, setImageSrc] = useState(images[0].image);
  const [selectedSize, setSelectedSize] = useState({});
  const [quantity, setQuantity] = useState(1);
  const [stockDisableState, setStockDisableState] = useState(false);

  useEffect(() => {
    if (Object.keys(selectedSize).length === 0) {
      if (details.product_stock <= 0) {
        setStockDisableState(true);
      } else {
        setStockDisableState(false);
      }
    } else {
      if (selectedSize.product_stock <= 0) {
        setStockDisableState(true);
      } else {
        setStockDisableState(false);
      }
    }

    return () => {
      // cleanup function
    };
  }, [selectedSize, details]);

  const { addProduct } = useContext(CartContext);
  const classes = useStyles();
  const history = useHistory();

  const mutation = useMutation((product) => notifyStock(product));

  const handleNotify = (product_id) => {
    mutation.mutate(
      { product_id: product_id },
      {
        onSuccess: () => {
          toast.info('You will be notified');
        },
        onError: (err) => {
          console.log('err', err);
          toast.info('Please login');
        },
      }
    );
  };

  const showPreviousImage = () => {
    if (imageNumber === 0) {
      return;
    }
    let previousImage = imageNumber - 1;
    setImageNumber(previousImage);
    setImageSrc(images[imageNumber].image);
  };

  const showNextImage = () => {
    if (imageNumber >= images.length - 1) {
      return;
    }
    let nextImage = imageNumber + 1;
    setImageNumber(nextImage);
    setImageSrc(images[imageNumber].image);
  };

  // const checkIfNotInStock = (productCount) => {
  //   if (productCount <= 0) {
  //     return true;
  //   } else {
  //     return false;
  //   }
  // };

  const handleQtyIncrease = () => {
    if (Object.keys(selectedSize).length !== 0) {
      if (quantity > selectedSize.product_stock - 1) {
        toast.error('Not enough stock!');
        // return;
      } else {
        setQuantity(quantity + 1);
      }
    } else {
      if (quantity > details.product_stock - 1) {
        toast.error('Not enough stock!');
        // return;
      } else {
        setQuantity(quantity + 1);
      }
    }
  };

  const handleQtyDecrease = () => {
    if (quantity === 1) {
      toast.info('Minimum quantity reached!');
      // setQuantity(1);
      // return;
    } else {
      setQuantity(quantity - 1);
    }
  };

  const showImagesPreview = (images) => {
    return images.map((img) => (
      <img
        src={`${process.env.REACT_APP_UPLOAD_URL}${img.image}`}
        alt={img.id}
        key={img.id}
        className={classes.previewImages_item}
        onClick={() => setImageSrc(img.image)}
      />
    ));
  };

  const addToFavouriteProduct = (product) => {
    const prevProducts = JSON.parse(localStorage.getItem('fav'));

    if (!prevProducts) {
      localStorage.setItem(
        'fav',
        JSON.stringify([
          {
            productId: product.id,
            productName: product.product_name,
            productImage: product.images[0].image,
            productBrand: product.brand && product.brand.brand_name,
            productPrice: product.price,
            bestSeller: product.bestSeller,
          },
        ])
      );
      toast.info('Product addded to favourites');
      return;
    }

    const isAlreadyFav = prevProducts.find(
      (fav) => product.id === fav.productId
    );

    if (isAlreadyFav) {
      return;
    }

    localStorage.setItem('fav', [
      JSON.stringify([
        ...prevProducts,
        {
          productId: product.id,
          productName: product.product_name,
          productImage: product.images[0].image,
          productBrand: product.brand && product.brand.brand_name,
          productPrice: product.price,
          bestSeller: product.bestSeller,
        },
      ]),
    ]);
    toast.info('Product addded to favourites');
  };

  const handleSizeSelect = (size) => {
    if (size.id === selectedSize.id) {
      setSelectedSize({});
    } else {
      setSelectedSize(size);
    }
    setQuantity(1);
  };

  const showOtherSizes = () => {
    if (othersizes.length === 0) {
      return <Typography>Not available</Typography>;
    } else {
      return othersizes.map((size) => (
        <Box key={size.id}>
          <Button
            variant={selectedSize.id === size.id ? 'contained' : 'outlined'}
            color='primary'
            style={{ marginBottom: '10px' }}
            onClick={() => handleSizeSelect(size)}
          >
            {size.product_size}

            <Typography
              varaint='subtitle'
              component='span'
              style={{
                color: selectedSize.id === size.id ? '#000' : '#808080',
                fontWeight: 'bold',
                fontSize: '0.9rem',
                paddingLeft: '10px',
              }}
            >
              ( NPR. {size.price - size.discount_amount})
            </Typography>
          </Button>
        </Box>
      ));
    }
  };

  const handleAddToCart = () => {
    if (Object.keys(selectedSize).length === 0) {
      const product = {
        ...details,
        quantity: quantity,
      };
      addProduct(product);
    } else {
      const productWithSelectedSize = {
        ...selectedSize,
        brand: { brand_name: details.brand.brand_name },
        images: details.images,
        quantity: quantity,
      };
      addProduct(productWithSelectedSize);
    }
    toast.dark('Product successfully added to cart!', {});
  };

  return (
    <>
      <Grid container spacing={2} justify='space-evenly'>
        <Grid item md={5}>
          <Box display='flex' alignItems='center'>
            <span onClick={showPreviousImage}>
              <ChevronLeftIcon
                style={{ cursor: 'pointer', fontSize: '2rem' }}
              />
            </span>
            <ReactImageMagnify
              {...{
                smallImage: {
                  alt: 'Wristwatch by Ted Baker London',
                  isFluidWidth: true,
                  src: `${process.env.REACT_APP_UPLOAD_URL}${imageSrc}`,
                },
                largeImage: {
                  src: `${process.env.REACT_APP_UPLOAD_URL}${imageSrc}`,
                  width: 1600,
                  height: 1600,
                },
                enlargedImageContainerStyle: {
                  zIndex: '10',
                },
              }}
            />

            <span onClick={showNextImage}>
              <ChevronRightIcon
                style={{ cursor: 'pointer', fontSize: '2rem' }}
              />
            </span>
          </Box>
          <div className={classes.previewImages}>
            {showImagesPreview(images)}
          </div>
        </Grid>
        <Grid item md={5}>
          <Box>
            <Box display='flex'>
              <Typography
                variant='h5'
                gutterBottom
                style={{
                  fontWeight: 'bold',
                  cursor: 'pointer',
                  textDecoration: 'underline',
                }}
                onClick={() => history.push(`/brand/${details.brand.id}`)}
              >
                {details.brand && details.brand.brand_name}
              </Typography>
              {details.best_seller && (
                <Chip
                  style={{
                    color: 'white',
                    marginLeft: '1rem',
                  }}
                  color='primary'
                  label='Best Seller'
                />
              )}
            </Box>
            {details.product_size ? (
              <Typography variant='h6' gutterBottom>
                {details.product_name}
                <Typography varaint='subtitle' component='span' gutterBottom>
                  {' '}
                  ( {details.product_size} )
                </Typography>
              </Typography>
            ) : (
              <Typography variant='h6' gutterBottom>
                {details.product_name}
              </Typography>
            )}
            {details.discount_amount === 0 ? (
              <Typography
                variant='h5'
                color='primary'
                style={{ fontWeight: 'bold' }}
                gutterBottom
              >
                NPR. {details.price}
              </Typography>
            ) : (
              <>
                <Box display='flex' alignItems='center'>
                  <Typography
                    variant='h6'
                    color='primary'
                    style={{
                      textDecoration: 'line-through',
                      fontSize: '1.5rem',
                    }}
                    gutterBottom
                  >
                    NPR. {details.price}
                  </Typography>
                  <Typography
                    variant='h6'
                    color='secondary'
                    style={{ marginBottom: '12px', marginLeft: '10px' }}
                  >
                    ( NRP.{details.discount_amount} off )
                  </Typography>
                </Box>
                <Typography
                  variant='h5'
                  color='primary'
                  style={{ fontWeight: 'bold' }}
                >
                  NPR. {details.price - details.discount_amount}
                </Typography>
              </>
            )}
            <Typography variant='subtitle2' gutterBottom>
              Inclusive of all taxes
            </Typography>
            <Box display='flex' style={{ marginBottom: '1rem' }}>
              <Rating
                value={details.average_rating}
                readOnly={true}
                precision={0.5}
                style={{ marginRight: '10px' }}
              />
              <Typography variant='subtitle' component='span' gutterBottom>
                {details.review_count} reviews
              </Typography>
            </Box>
            <Box display='flex' style={{ marginTop: '1.5rem' }}>
              <Typography varaint='subtitle' component='span' gutterBottom>
                Other Sizes:
              </Typography>

              <Box style={{ marginBottom: '16px', marginLeft: '1rem' }}>
                {showOtherSizes()}
              </Box>
            </Box>
            <Box
              display='flex'
              alignItems='center'
              style={{ marginBottom: '1rem' }}
            >
              <Typography style={{ marginRight: '2rem' }}>QTY: </Typography>
              <ButtonGroup
                disabled={stockDisableState}
                variant='outlined'
                color='primary'
              >
                <Button onClick={() => handleQtyDecrease()}>-</Button>
                <Button>{quantity}</Button>
                <Button onClick={() => handleQtyIncrease()}>+</Button>
              </ButtonGroup>
            </Box>
            <Box>
              <IconButton
                onClick={() => addToFavouriteProduct(details)}
                style={{
                  marginRight: '1rem',
                  marginBottom: '1rem',
                }}
              >
                <FavoriteBorderIcon />
              </IconButton>
              <Button
                variant='contained'
                color='primary'
                disabled={stockDisableState}
                onClick={() => handleAddToCart()}
              >
                ADD TO CART
              </Button>
              {/* {checkIfNotInStock(details.product_stock) && (
                <Typography
                  variant='h6'
                  color='primary'
                  style={{ cursor: 'pointer', textDecoration: 'underline' }}
                  onClick={() => handleNotify(details.id)}
                >
                  Notify me when in stock
                </Typography>
              )} */}
              {stockDisableState && (
                <Typography
                  variant='h6'
                  color='primary'
                  style={{ cursor: 'pointer', textDecoration: 'underline' }}
                  onClick={() => handleNotify(details.id)}
                >
                  Notify me when in stock
                </Typography>
              )}
            </Box>
            <Typography color='secondary' variant='subtitle1'>
              100% Genuine Product
            </Typography>
          </Box>
        </Grid>
      </Grid>
      <div className={classes.productDesc}>
        <Typography style={{ fontWeight: 'bold' }} gutterBottom>
          Description
        </Typography>
        <Typography gutterBottom>{details.product_desc}</Typography>
        {details.how_to_use && (
          <>
            <Typography style={{ fontWeight: 'bold' }} gutterBottom>
              How to use
            </Typography>
            <Typography>{details.how_to_use}</Typography>
          </>
        )}
        <Divider style={{ margin: '1rem 0' }} />
      </div>
    </>
  );
};

export default ProductInfo;
