import {
  Box,
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardMedia,
  Chip,
  makeStyles,
  Typography,
} from '@material-ui/core';
import React from 'react';
import { useHistory } from 'react-router';

const useStyles = makeStyles((theme) => ({
  root: {
    position: 'relative',
    width: 300,
    [theme.breakpoints.down('xs')]: {
      minWidth: '90%',
    },
    margin: '1rem',
  },
}));

const OrderProductCard = ({
  name,
  price,
  image,
  id,
  brandName,
  bestSeller,
  discount,
  quantity,
}) => {
  const classes = useStyles();
  const history = useHistory();

  return (
    <Card
      className={classes.root}
      onClick={() => {
        history.push(`/product/${id}`);
      }}
    >
      <CardActionArea>
        <CardMedia
          component='img'
          alt={name}
          height='190'
          style={{ objectFit: 'contain' }}
          src={image}
          title={name}
        />
        <CardContent>
          <Typography gutterBottom variant='h6' component='h2'>
            {name}
          </Typography>
          <Typography variant='subtitle1' color='primary' component='p'>
            {brandName}
          </Typography>
          {bestSeller && (
            <Chip
              style={{ position: 'absolute', top: 5, right: 5, color: 'white' }}
              color='primary'
              label='Best Seller'
            />
          )}
        </CardContent>
      </CardActionArea>
      <CardActions
        style={{
          display: 'flex',
          justifyContent: 'flex-end',
        }}
      >
        {discount > 0 ? (
          <Typography variant='h6' color='primary'>
            NPR. {price - discount}
          </Typography>
        ) : (
          <Typography variant='h6' color='primary'>
            NPR. {price}
          </Typography>
        )}

        {discount > 0 ? (
          <Chip
            color='primary'
            label={`NPR. ${discount} off`}
            style={{ color: 'white', marginLeft: '1rem' }}
          />
        ) : null}

        {
          <Chip
            color='primary'
            label={`Qty: ${quantity}`}
            style={{ position: 'absolute', left: 0, bottom: '10px' }}
          />
        }
      </CardActions>
    </Card>
  );
};

export default OrderProductCard;
