import {
  Container,
  FilledInput,
  FormControl,
  InputAdornment,
  InputLabel,
  Typography,
  Button,
  makeStyles,
} from '@material-ui/core';

import React from 'react';
import { useMutation, useQuery } from 'react-query';
import { toast } from 'react-toastify';
import Loading from '../components/loading';
import about from '../services/kbs/about';
import contact from '../services/kbs/contact';

const useStyles = makeStyles((theme) => ({
  form_item: {
    marginBottom: '1rem',
  },
}));

const Contact = () => {
  const classes = useStyles();
  const { isLoading, data } = useQuery('about', about);

  const mutation = useMutation((info) => contact(info));

  const handleContactSubmit = (e) => {
    e.preventDefault();
    const name = e.target.name.value;
    const email = e.target.email.value;
    const phone = e.target.phone.value;
    const subject = e.target.subject.value;
    const message = e.target.message.value;

    if (
      name.length < 4 ||
      email.length < 6 ||
      subject.length < 6 ||
      message.length < 6
    ) {
      toast.error('Please provide all information');
      return;
    }

    mutation.mutate(
      { name, email, phone_number: phone, subject, message },
      {
        onSuccess: () => {
          console.log('suceess');
          toast.info('We will get back to you soon!');
        },
      }
    );
  };

  if (isLoading) {
    return <Loading />;
  }

  return (
    <Container>
      <div style={{ minHeight: '70vh' }}>
        <Typography
          variant='h4'
          align='center'
          style={{ marginTop: '3rem', marginBottom: '2rem', color: 'grey' }}
        >
          CONTACT US
        </Typography>

        <Typography align='center' gutterBottom>
          {data[0].contact_us}
        </Typography>
        <Typography align='center' gutterBottom variant='h6'>
          Phone Number: {data[0].phone_number}
        </Typography>

        <form onSubmit={handleContactSubmit} style={{ marginBottom: '2rem' }}>
          <FormControl
            variant='filled'
            color='secondary'
            fullWidth={true}
            className={classes.form_item}
          >
            <InputLabel>Enter Your Full Name</InputLabel>
            <FilledInput
              type='text'
              margin='dense'
              name='name'
              style={{ backgroundColor: 'white' }}
              endAdornment={
                <InputAdornment position='end'>
                  <i className='fas fa-user'></i>
                </InputAdornment>
              }
            />
          </FormControl>
          <FormControl
            variant='filled'
            color='secondary'
            fullWidth={true}
            className={classes.form_item}
          >
            <InputLabel>Enter Email</InputLabel>
            <FilledInput
              type='email'
              margin='dense'
              name='email'
              style={{ backgroundColor: 'white' }}
              endAdornment={
                <InputAdornment position='end'>
                  <i className='fas fa-envelope'></i>
                </InputAdornment>
              }
            />
          </FormControl>
          <FormControl
            variant='filled'
            color='secondary'
            fullWidth={true}
            className={classes.form_item}
          >
            <InputLabel>Phone Number</InputLabel>
            <FilledInput
              type='number'
              margin='dense'
              name='phone'
              style={{ backgroundColor: 'white' }}
              endAdornment={
                <InputAdornment position='end'>
                  <i className='fas fa-phone'></i>
                </InputAdornment>
              }
            />
          </FormControl>
          <FormControl
            variant='filled'
            color='secondary'
            fullWidth={true}
            className={classes.form_item}
          >
            <InputLabel>Subject</InputLabel>
            <FilledInput
              type='text'
              margin='dense'
              name='subject'
              style={{ backgroundColor: 'white' }}
              endAdornment={
                <InputAdornment position='end'>
                  <i className='fas fa-envelope'></i>
                </InputAdornment>
              }
            />
          </FormControl>
          <FormControl
            variant='filled'
            color='secondary'
            fullWidth={true}
            className={classes.form_item}
          >
            <InputLabel>Message</InputLabel>
            <FilledInput
              type='text'
              margin='dense'
              name='message'
              style={{ backgroundColor: 'white' }}
              multiline
              rows={2}
              endAdornment={
                <InputAdornment position='end'>
                  <i className='far fa-sticky-note'></i>
                </InputAdornment>
              }
            />
          </FormControl>
          <Button
            variant='contained'
            color='primary'
            fullWidth
            style={{ margin: '1rem 0' }}
            type='submit'
          >
            Submit
          </Button>
        </form>
      </div>
    </Container>
  );
};

export default Contact;
